.main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;

  &Module {
    position: relative;
    width: 100%;
  }

  &Background {
    position: relative;
    overflow: hidden;
    top: 0;
    left: 0;
    width: 100%;
    z-index: -1;
    height: 100vh;

    &Image {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      -o-object-fit: cover;
         object-fit: cover;
      -o-object-position: center center;
         object-position: center center;
      opacity: 1;
      -webkit-transition: opacity 500ms ease 0s;
      transition: opacity 500ms ease 0s;
    }

    &Dimmer {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: #000;
      opacity: 0.5;
    }

    &Caption {
      position: absolute;
      left: 0;
      top: 45%;
      width: 100%;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      text-align: center;
      /**/
      color: #fff;

      &PrimaryHeader {
        margin: 0;
        padding: 0 0 10px;
        font-size: 48px;
        font-weight: 300;
        line-height: 1.2;
        letter-spacing: 5.5px;
        text-transform: uppercase;
      }

      &SecondaryHeader {
        margin: 0;
        padding: 0 0 10px;
        font-size: 12px;
        font-weight: 500;
        line-height: 1.5;
        letter-spacing: 3.5px;
        text-transform: uppercase;
        opacity: 0.7;
      }
    }
  }
}
